.myReturn{
    text-decoration: none;
    position: fixed;
    left: 10px;
    top: 10px;
    width: 120px;
    height: 50px;
    background: #8b1ed8;
    color: wheat;
    font-family: Arial, serif;
    text-align: center;
    border: 2px solid black;
    border-radius: 25px;
    transition: all 0.5s;
}

.scale{
    transform: scale(130%);
}

.myReturn:hover{
    transform: scale(110%);
    background: #21004b;
}