.skills{
    position: fixed;
    top:0;
    right:0;
    bottom:0;
    left:0;
    z-index: 2;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
    background: black;
    opacity: 90%;
}

.skills__count{
    align-self: center;
    flex-grow: 0.3;
    width: 50%;
}

@media (max-width: 1000px) {
    .skills{
        position: fixed;
        top:0;
        right:0;
        bottom:0;
        left:0;
        z-index: 2;
        flex-direction: column;
        display: flex;
        justify-content: space-around;
        text-align: center;
        background: black;
        opacity: 90%;
    }

    .skills__count{
        margin: auto;
        align-self: center;
        width: 80%;
    }
}