.myButton {
    text-decoration: none;
    color: #FFFFE8;
    display: inline-block;
    position: relative;
    padding: 15px 30px;
    border: 1px solid;
    background: rgba(255, 255, 255, 0.1);
    font-family: 'Montserrat', sans-serif;
    border-image: linear-gradient(180deg, #34C3D5, #B8B3E9, #8B1ED8);
    border-image-slice: 1;
    text-transform: uppercase;
    overflow: hidden;
    letter-spacing: 2px;
    transition: .8s cubic-bezier(.165, .84, .44, 1);
}
.myButton:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 0;
    width: 100%;
    z-index: -1;
    color: white;
    background: linear-gradient(180deg, #34C3D5, #B8B3E9, #8B1ED8);
    transition: .8s cubic-bezier(.165, .84, .44, 1);
}
.myButton:hover {
    background: rgba(255, 255, 255, 0.1);
}
.myButton:hover:before {
    bottom: 0;
    top: auto;
    height: 100%;
}