header{
    background: url("../../images/bg.jpg")  no-repeat;
    background-size: cover;
    display: flex;
    height: 100vh;
}

.header_text{
    line-height: 1.3;
    font-family: 'Bebas Neue', cursive;
    font-size: 45px;
    align-self: flex-end;
    padding: 100px;
}

@media (max-width: 1000px) {
    header{
        background: url("../../images/bg.jpg")  no-repeat;
        background-size: cover;
        display: flex;
        height: 70vh;
    }

    .header_text{
        line-height: 1;
        left: 0;
        bottom: 0;
        font-family: 'Bebas Neue', cursive;
        font-size: 30px;
        align-self: flex-end;
        padding: 100px;
    }
}