@media (max-width: 1000px) {
    .myDescription{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        text-align: center;
    }

    .myDescription>*{
        padding: 20px 5px;
    }
}

@media (min-width: 1000px) {
    .myDescription{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        padding-bottom: 30px;
        width: 30%;
    }

    .myDescription>*{
        align-self: flex-start;
    }
}