#portfolio{
    flex-direction: column;
    text-align: center;
}


.project h3{
    font-family: 'Mochiy Pop P One', sans-serif;
    font-size: 25px;
}

@media (min-width: 1000px){

    #portfolio img{
        max-height: 300px;
        border-radius: 15px;
    }

    .project, #portfolio{
        padding: 50px 0;
    }

    .project{
        display: flex;
        justify-content: space-around;
    }

    .project:nth-child(odd){
        flex-direction: row-reverse;
    }

    .project_description_block{
        width: 35%;
        display: flex;
        flex-direction: column;
    }

    .project_description_block h3{
        flex-grow: 3;
    }

    .project_description_block p{
        flex-grow: 4;
    }
}

@media (max-width: 1000px) {
    #portfolio img{
        display: none;
    }

    .project{
        padding: 25px 0;
        display: flex;
        justify-content: space-around;
    }

    .project_description_block{
        margin: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }

    .project_description_block>*{
        padding: 20px 0;
    }
}