.navbar{
    background: #210044;
    position: fixed;
    z-index: 1;
    width: 100%;
    opacity: 80%;
}

.navbar ul{
    display: flex;
    justify-content: flex-end;
    margin: auto;
    padding-right: 70px;
    list-style: none;
}

.nav_item:hover{
    background-color: #b957fa;
    transition: 0.3s;
}

.nav_item{
    display: block;
    padding: 20px 40px;
    text-decoration: none;
}

@media (max-width: 1000px) {
    .navbar{
        display: none;
    }
}