.container{
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: #336699;
}

.piano{
    margin: 15% auto;
}

.piano kbd{
    font-size: 15px;
    font-weight: 900;
}

.whiteKeys kbd{
    padding: 150px 35px 10px 35px;
    background: white;
    color: black;
    border: 3px solid black;
}

.blackKeys kbd{
    padding: 100px 20px 10px;
    margin-left: 10px;
    margin-right: 10px;
    background: black;
    color: white;
    border: 3px solid black;
}

.whiteKeys{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 10px;
}

.blackKeys .leftKeys{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-148%, -175%);
    padding: 10px;
}

.blackKeys .rightKeys{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(3%, -175%);
    padding: 10px;
}

@media (max-width: 700px) {
    .container{
        transform: rotate(90deg);
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background: #FFF;
    }

    .whiteKeys{
        position: absolute;
        left: 50%;
        top: 60%;
        transform: translate(-50%, -50%);
        padding: 10px;
    }

    .blackKeys .leftKeys{
        position: absolute;
        left: 50%;
        top: 60%;
        transform: translate(-148%, -175%);
        padding: 10px;
    }

    .blackKeys .rightKeys{
        position: absolute;
        left: 50%;
        top: 60%;
        transform: translate(3%, -175%);
        padding: 10px;
    }
}