@media (min-width: 1000px){
    .about{
        display: flex;
        padding: 100px;
        flex-direction: row;
        justify-content: space-around;
    }

    .about__photo{
        height: 400px;
        border-radius: 30px;
    }
}

@media (max-width: 1000px) {
    .about{
        display: flex;
        flex-direction: column;
    }

    .about__photo{
        height: 250px;
        width: 250px;
        margin: 20px auto;
        border-radius: 30px;
    }
}