.space {
    background-clip: border-box;
    margin: 0;
    overflow: hidden;
    position: absolute;
    background: url("images/space.png") ;
    height: 100%;
    width: 100%;
    z-index: 0;
}

.submit{
    height: 30px;
    background: crimson;
    transition: background-color 0.7s;
}

.planetArea {
    position: absolute;
    bottom: 0;
    left: 0;
}

.planet {
    position: relative;
    width: 600px;
    height: 600px;
    margin-left: -10px;
    margin-bottom: -10px;
    z-index: 2;
}

.rocket {
    position: absolute;
    width: 256px;
    height: 256px;
    left: 270px;
    bottom: 270px;
    transform: rotate(33deg);
    z-index: 1;
    animation-duration: 7s;
}

.controlPanel {
    background:linear-gradient(27deg, darkcyan, lightseagreen);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 400px;
    height: 400px;
    position: absolute;
    bottom: 5vh;
    right: 5vh;
}

.password{
    border-color: red;
    border-radius: 10px;
    border-width: 3px;
    color: crimson;
    width: 85%;
    margin:auto;
    padding: 5px;
    background-color: darkslategray;
}

.submit:hover{
    background-color: orangered;
}

.checkButtons, .ranges {
    margin: auto;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    width: 90%;
}

.checkButtons > input{
    transform: scale(2);
}

.ranges {
    height: 200px;
}

.ranges > input {
    margin-top: 10px;
    margin-bottom: 10px;
    max-width: 15%;
    -webkit-appearance: slider-vertical;
}

.launch{
    transition: all 1s;
    height: 30px;
    background: crimson;
}

.launch:disabled{
    background: black;
}

.launch:enabled:hover{
    background-color: aqua;
    color:black
}

@keyframes flying {
    0%
    {left:270px;
     bottom:270px;
    }
    100% {
        left:800px;
        bottom:1000px;
    }
}

.flying{
    animation: 7s flying forwards;
}